@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500&display=swap);
html{font-family:'Montserrat', sans-serif;scroll-behavior:smooth}

.About{box-sizing:border-box;padding:30px;display:grid;grid-gap:50px;gap:50px}@media screen and (min-width: 768px){.About{grid-template-columns:1fr 1fr}}

.Container{box-sizing:border-box;width:100%;max-width:1440px;height:100%;margin:0 auto}@media screen and (min-width: 768px){.Container{padding:60px}}.Container.centered{display:flex;align-items:center}.Container.flexEnd{justify-content:flex-end}

.Screen{width:100%;height:100%;display:flex;align-items:center}

.Timeline{position:relative;margin:0 auto;padding:.25em 0 2em;font-family:'Montserrat', sans-serif}.Timeline.scale:before{content:"";border-left:4px solid #47423b;left:-0.25em;top:2em;height:100%;position:absolute;bottom:0;top:0;transform-origin:0 0;-webkit-animation:scaleVertical 1.4s .85s ease both 1;animation:scaleVertical 1.4s .85s ease both 1}.Timeline.scale:after{content:"";clear:both;position:absolute;bottom:0px;left:-10px;width:16px;height:16px;border-radius:50%;background:none repeat scroll 0% 0% #47423b;-webkit-animation:revealScaleUp 0.5s 1.8s ease both 1;animation:revealScaleUp 0.5s 1.8s ease both 1}@-webkit-keyframes scaleVertical{0%{transform:scaleY(0)}100%{transform:scaleY(1)}}@keyframes scaleVertical{0%{transform:scaleY(0)}100%{transform:scaleY(1)}}

.TimelineItem{list-style:none;margin:3em 0;padding:0 0 0 2em;position:relative;color:#47423b}.TimelineItem.reveal::before{content:"";position:absolute;top:50%;left:-14px;width:16px;height:16px;border-radius:50%;background:none repeat scroll 0% 0% #47423b;border:4px solid #f2f2f2;-webkit-animation:timelineRevealScaleUp 0.25s 0s ease both 1;animation:timelineRevealScaleUp 0.25s 0s ease both 1}.TimelineItem.reveal .TimelineItem--content{-webkit-animation:timelineRevealFromLeft 0.5s 0s ease both 1;animation:timelineRevealFromLeft 0.5s 0s ease both 1}.TimelineItem.reveal:nth-child(1):before{-webkit-animation-delay:0.85s;animation-delay:0.85s}.TimelineItem.reveal:nth-child(1) .TimelineItem--content{-webkit-animation-delay:1.05s;animation-delay:1.05s}.TimelineItem.reveal:nth-child(2):before{-webkit-animation-delay:1.2s;animation-delay:1.2s}.TimelineItem.reveal:nth-child(2) .TimelineItem--content{-webkit-animation-delay:1.5s;animation-delay:1.5s}.TimelineItem:first-child{margin-top:0}.TimelineItem--date{font-size:14px;font-weight:500;margin-bottom:.5em}.TimelineItem--org{font-size:13px;font-weight:500;color:#7c7c7c;margin:0}.TimelineItem--position{font-size:14px;margin:.5em 0;font-weight:500}.TimelineItem--desc{font-weight:400;font-size:13px;line-height:1.5}@-webkit-keyframes timelineRevealFromLeft{0%{transform:translateX(-1em);opacity:0}100%{opacity:1;transform:translateX(0)}}@keyframes timelineRevealFromLeft{0%{transform:translateX(-1em);opacity:0}100%{opacity:1;transform:translateX(0)}}@-webkit-keyframes timelineRevealScaleUp{0%{opacity:0;transform:scale(0.5)}50%{opacity:1;transform:scale(1.3)}100%{opacity:1;transform:scale(1)}}@keyframes timelineRevealScaleUp{0%{opacity:0;transform:scale(0.5)}50%{opacity:1;transform:scale(1.3)}100%{opacity:1;transform:scale(1)}}

.UnderlinedTitle{position:relative;font-family:'Montserrat', sans-serif;text-transform:uppercase;font-size:16px;font-weight:400;padding-bottom:10px;margin:0 0 2em 0}.UnderlinedTitle::after{content:"";width:90%;position:absolute;bottom:0;left:0;height:2px;background-image:linear-gradient(90deg, #47423b, #47423b 2px, transparent 2px, transparent 6px);background-size:6px 2px}

.SkillDots{display:flex;align-items:center;justify-content:flex-start;perspective:0}.SkillDots.rotate .SkillDots--item--back{-webkit-backface-visibility:hidden;backface-visibility:hidden;background-color:#47423b;position:absolute;height:100%;width:100%;border-radius:50%;transform:rotateY(180deg)}.SkillDots.rotate .SkillDots--item.active{transform-style:preserve-3d;-webkit-animation:rotate 1.6s ease-in-out forwards;animation:rotate 1.6s ease-in-out forwards}.SkillDots.rotate .SkillDots--item.active:nth-child(1){-webkit-animation-delay:.3s;animation-delay:.3s}.SkillDots.rotate .SkillDots--item.active:nth-child(2){-webkit-animation-delay:.4s;animation-delay:.4s}.SkillDots.rotate .SkillDots--item.active:nth-child(3){-webkit-animation-delay:.5s;animation-delay:.5s}.SkillDots.rotate .SkillDots--item.active:nth-child(4){-webkit-animation-delay:.6s;animation-delay:.6s}.SkillDots.rotate .SkillDots--item.active:nth-child(5){-webkit-animation-delay:.7s;animation-delay:.7s}.SkillDots.rotate .SkillDots--item.active:nth-child(6){-webkit-animation-delay:.8s;animation-delay:.8s}.SkillDots.rotate .SkillDots--item.active:nth-child(7){-webkit-animation-delay:.9s;animation-delay:.9s}.SkillDots.rotate .SkillDots--item.active:nth-child(8){-webkit-animation-delay:1s;animation-delay:1s}.SkillDots--item{position:relative;height:16px;width:16px;border-radius:50%;background-color:#c9c8c9;margin-left:.25em}.SkillDots--item:first-child{margin:0}@-webkit-keyframes rotate{100%{transform:rotateY(180deg)}}@keyframes rotate{100%{transform:rotateY(180deg)}}

.SkillsItem{display:grid;grid-template-columns:1fr 1fr;grid-gap:15px;gap:15px}.SkillsItem--label{font-size:16px;font-weight:300;margin:.5em 0}

.Skills.appear .Skills--content{-webkit-animation:skillsAppear .25s .75s ease-in-out both;animation:skillsAppear .25s .75s ease-in-out both}@-webkit-keyframes skillsAppear{0%{opacity:0;transform:translateX(3em)}100%{opacity:1;transform:translateX(0)}}@keyframes skillsAppear{0%{opacity:0;transform:translateX(3em)}100%{opacity:1;transform:translateX(0)}}

.WideFrame{box-sizing:border-box;width:100%;height:100%;background-color:#ffffff}@media screen and (min-width: 768px){.WideFrame{border:35px solid black;padding:60px;border-radius:2px;box-shadow:7px 7px 22px rgba(0,0,0,0.7),inset 4px 8px 20px rgba(0,0,0,0.6)}}@media screen and (min-width: 1024px){.WideFrame{max-height:750px;max-width:550px}}.WideFrame-white{border-color:#f4f1f2;background-color:#ededed}.WideFrame-white .WideFrame--content{background:#fafafa;color:#47423b}.WideFrame--content{box-sizing:border-box;background:linear-gradient(140deg, #121212 10%, #000);color:#ffffff;height:100%;display:flex;align-items:flex-end;justify-content:flex-start;padding:30px}@media screen and (min-width: 768px){.WideFrame--content{box-shadow:inset 1px 1px 5px rgba(0,0,0,0.3)}}.WideFrame--content.centered{align-items:center;justify-content:center}

.Presentation>h3{font-size:26px;font-weight:400;text-transform:uppercase}.Presentation>h4{text-transform:uppercase;font-size:17px;font-weight:400}.Presentation>a{transition:opacity .3s;opacity:.8;margin-right:.5em}.Presentation>a:hover{opacity:1}

.NarrowFrame{box-sizing:border-box;width:100%;padding:20px}@media screen and (min-width: 768px){.NarrowFrame{box-shadow:7px 7px 22px rgba(0,0,0,0.7),inset 4px 8px 20px rgba(0,0,0,0.6);border:25px solid #404040;border-radius:2px;height:100%;background-color:#ededed;padding:40px}}.NarrowFrame--content{position:relative;box-sizing:border-box;background:#ffffff;padding:0;box-shadow:1px 1px 4px rgba(0,0,0,0.3)}@media screen and (min-width: 768px){.NarrowFrame--content{height:100%;display:flex;align-items:center;justify-content:center;box-shadow:none}.NarrowFrame--content::after{content:"";position:absolute;height:100%;width:100%;box-shadow:inset 2px 2px 4px rgba(0,0,0,0.3)}}.NarrowFrame--content>img{width:100%;height:100%}.NarrowFrame--content.center{align-items:center;justify-content:center}

.Description{z-index:0;padding:30px}.Description>p{color:#47423b;font-size:14px;line-height:1.5}

.Tag{display:inline-block;color:#ffffff;background-color:#47423b;font-size:12px;font-weight:600;text-transform:uppercase;padding:.4em;border-radius:2px}

.TagList{margin-bottom:1.5em;margin-top:-1.25em}.TagList>.Tag{margin-right:.5em;margin-bottom:.5em}.TagList>.Tag:last-child{margin:0}

.Work{overflow-x:hidden;padding:20px 0}@media screen and (min-width: 768px){.Work{display:flex;overflow-x:unset;flex-flow:row wrap}}@media screen and (min-width: 1024px){.Work{flex-flow:row nowrap}}.Work>.NarrowFrame{z-index:10}@media screen and (min-width: 768px){.Work>.NarrowFrame{flex:0 0 95%;margin-bottom:2em;margin-left:auto;margin-right:auto}}@media screen and (min-width: 1024px){.Work>.NarrowFrame{margin:0;flex:1 1 60%;transform:translateY(-40px)}}@media screen and (min-width: 1024px){.Work>.NarrowFrame.revealFromLeft{-webkit-animation:narrowFrameRevealFromLeft .5s .35s ease-in-out both;animation:narrowFrameRevealFromLeft .5s .35s ease-in-out both}}.Work>.Description{position:relative}@media screen and (min-width: 768px){.Work>.Description{flex:0 0 100%}}@media screen and (min-width: 1024px){.Work>.Description{background-color:#f2f2f2;flex:1 1 40%;transform:translateY(40px)}.Work>.Description::before{content:"";background-color:#f2f2f2;position:absolute;top:0;left:-50%;height:100%;width:50%}}@media screen and (min-width: 1024px){.Work>.Description.revealFromRight{-webkit-animation:descriptionRevealFromRight .5s .35s ease-in-out both;animation:descriptionRevealFromRight .5s .35s ease-in-out both}}@-webkit-keyframes descriptionRevealFromRight{0%{opacity:0.3;transform:translateX(3em) translateY(40px)}100%{opacity:1;transform:translateX(0) translateY(40px)}}@keyframes descriptionRevealFromRight{0%{opacity:0.3;transform:translateX(3em) translateY(40px)}100%{opacity:1;transform:translateX(0) translateY(40px)}}@-webkit-keyframes narrowFrameRevealFromLeft{0%{opacity:0.3;transform:translateX(-3em) translateY(-40px)}100%{opacity:1;transform:translateX(0) translateY(-40px)}}@keyframes narrowFrameRevealFromLeft{0%{opacity:0.3;transform:translateX(-3em) translateY(-40px)}100%{opacity:1;transform:translateX(0) translateY(-40px)}}

.Link{display:inline-flex;align-items:center;justify-content:flex-start;text-decoration:none;color:#47423b;font-size:12px;font-weight:500}.Link:hover{text-decoration:underline}.Link>img{margin-right:.5em;height:20px}

.WorkLinks{margin:2em 0;padding:0}.WorkLinks>li{list-style:none;margin:.5em 0}

.Thanks{text-align:center;margin:0 auto;padding:15px 30px;color:#ffffff}.Thanks>h1{font-weight:300;font-size:28px;margin-bottom:2em}.Thanks--link{margin-bottom:3em}.Thanks--link:last-of-type{margin-bottom:0}.Thanks--link>p{margin:.5em 0}.Thanks--link>a{color:#ffffff}

